              </el-form-item>
              <el-form-item label="证件号码">
                <el-input v-model="formInline.value2" clearable placeholder="根据证件号码查询"></el-input>
              </el-form-item>
              <el-form-item label="订单号">
                <el-input v-model="formInline.value3" clearable placeholder="根据审核人员查询"></el-input>
              </el-form-item>
              <el-form-item label="体检机设备编号">
                <el-input v-model="formInline.value4" clearable placeholder="根据设备编号查询"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="openVisible1">下载对账单</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="24" style="margin-top:10px;">
            <el-table :row-class-name="tableRowClassName" :data="tableData" border onselectstart="return false">
              <el-table-column fixed width="50" prop="lists" label="序号" align="center"></el-table-column>
              <el-table-column width="130" prop="serial" label="流水号" align="center"></el-table-column>
              <el-table-column width="130" prop="serial" label="订单号  " align="center"></el-table-column>
              <el-table-column width="130" prop="name" label="姓名" align="center"></el-table-column>
              <el-table-column width="200" prop="idcard" label="证件号码" align="center"></el-table-column>
              <el-table-column width="100" label="基本信息" align="center">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="top">
                    <p style="font-size:17px;color:#000;">基本信息</p>
                    <p>姓名: {{ scope.row.name }}</p>
                    <p>证件号码: {{ scope.row.idcard }}</p>
                    <p>体检机设备编号: {{ scope.row.equipment }}</p>
                    <p>订单号: 20191026101351338607</p>
                    <p>收款账户: 2019062065593577</p>
                    <p>备注: </p>
                    <div slot="reference" class="name-wrapper">
                      <el-tag size="medium">悬停查看</el-tag>
                    </div>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column width="100" label="缴费方式" align="center">
                <template slot-scope="scope">
                  <el-tag effect="dark" :type="scope.row.payment === '支付宝' ? '' : 'success'" disable-transitions>{{scope.row.payment}}</el-tag>
                </template>
              </el-table-column>
              <el-table-column width="100" label="缴费金额" align="center">
                <template slot-scope="scope">
                  <i style="color: red;" class="el-icon-qian"></i><span style="margin-left: 3px;">{{scope.row.payCost}}</span>
                </template>
              </el-table-column>
              <el-table-column width="100" label="缴费类型" align="center">
                <template>
